.btn.btn-back-Table{
    background: transparent;
    color: #266e7b;
    padding: 8px 30px;
    border-radius: 10px;
    margin-left: 0px;
    margin: 0px;
    border: 1px solid #266e7b;
    box-shadow: none;
    text-transform: inherit;
}

.btn.btn-add-patient {
    background: #266e7b;
    box-shadow: none;
    border-radius: 8px;
    text-transform: capitalize;
    padding: 8px 20px;
    color: #fff;
}

.Patient-text{margin: 12px 0px; font-weight: 500;}

.MuiTableCell-head{background: #f2f7f8 !important; border:none;}

.MuiTableCell-root p{margin: 0px;}

.MuiPaper-root.MuiPaper-elevation4.MUIDataTable-paper-2.MuiPaper-rounded {
    box-shadow: none;
    border: 1px solid #e8edf2;
    background: #ffffff;
    border-radius: 18px;
    overflow: hidden;
    margin-top: 20px;
}

.MUIDataTablePagination-root-55:last-child {
    padding: 0px !important;   
}

a.btn-edit, button.btn-edit, div.btn-edit {
    background: #4daf51;
    padding: 5px;
    width: 30px;
    height: 30px;
    border: 1px solid #4CAF50;
    text-align: center;
    border-radius: 25px !important;
    margin: 0 2px;
    color: #fff;
    display: inline-block;
}

.btn-close-D{
    background: #f43a3a;
    padding: 4px 5px;
    width: 30px;
    height: 30px;
    border: 1px solid #f43a3a;
    text-align: center;
    border-radius: 25px;
    margin: 0 2px;
    color: #fff;
    display: inline-block;
}

.MuiTableCell-body {
    color: rgb(106 106 106 / 87%);
    font-family: inherit !important;
}

.MuiTableCell-root{font-family: inherit;}

.MuiTableCell-root {
    padding: 15px 15px !important;
}



.btn.addBtn.btn-no{}

button.btn.addBtn.btn-no {
    background: #fff;
    padding: 8px 30px;
    box-shadow: none;
    color: #266e7b;
    border-radius: 8px;
    text-transform: inherit;
    border: 1px solid #266e7b;
}


button.btn.addBtn.btn-yes {
    background: #266e7b;
    padding: 8px 30px;
    box-shadow: none;
    color: #fff;
    border-radius: 8px;
    text-transform: inherit;
}


.Popup-Modal-YesNo.modal-dialog .modal-content{overflow: inherit; border-radius: 18px;}